import axios from "axios";
import { endpoints } from "Constants/api";
import axiosHelpers from "Constants/axiosHelpers";

export const addPropertyUpdateStayType = async (data) => {
    const request = await axios({
        method: "put",
        url: endpoints.stayDetails.ADD_UPDATE_STAY_TYPE,
        headers: {
            // Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
        data: data,
    }).catch();
    return request;
};


export const addPropertyCreateRoomType = async (data, isCreate) => {
    const request = await axios({
        method: isCreate ? "post" : "put",
        url: endpoints.stayDetails.ADD_DEFINE_ROOM_TYPE,
        headers: {
            // Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
        data: data,
    })
        .catch();
    return request;
};

export const getStayTypeDetails = async (propertyId) => {
    const request = await axios({
        method: "get",
        url: endpoints.stayDetails.GET_STAY_TYPE + `/${propertyId}`,
        headers: {
            // Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
    }).catch(axiosHelpers);
    return request;
}


export const getDefineRoomTypeList = async (id) => {
    const request = await axios({
        method: "get",
        url: endpoints.serviceAmenity.GET_ROOM_TYPE + `/${id}`,
        headers: {
            // Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
    }).catch(axiosHelpers);
    return request;
}